@import '../options/variables.scss';

/* class action */
.active-pop {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  width: 40px;
  height: 40px;

  border-radius: 5px;
  transition: all 0.5s ease-in-out;

  i {
    color: $colorWhite !important;

  }
}

/* stop class action */
.myhead{
  background-color: white;

.nav-bar-home {
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;

  /* larg screen */
  .larg-screen {

    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;

    .right {
      display: flex;
      z-index: 1000;

      .button {
        margin-left: 10px;
        margin-right: 10px;
        position: relative;
        .flex-title {
          display: flex;
          justify-content: center;
          align-items: center;

          i {
            padding-top: 5px;
            font-size: $sizeIconsSmall;
          }

          .name {
            color: $colorBlack;
            font-size: $sizeMainText;
            font-weight: bold;
            padding-right: 5px;
            text-transform: capitalize;
            text-decoration: none;
          }
        }


        .fa-angle-down {
          color: $colorSubBlack;
        }

        /* popMenue */
        .pop-menue {
          position: absolute;
          left:-90px;
          width: 190px;
          padding-left: 15px;
          padding-top: 30px;
          transform: scale(0);
          transition: all 0.3s;
          background-color: $colorWhite;
          z-index: 999;

          div {
            width: 100%;
            height: 45px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            border-bottom: 1px solid rgb(186, 185, 185);

            p {
              color: $colorBlack;
              font-size: $sizeMainText;
              text-transform: capitalize;
            }

            &:hover .active-title {
              color: #5191FA;
              transition: all 0.2s;
            }

            &:hover {
              cursor: pointer;

            }

          }




        }

        /* stop popMemue */
        &:hover .flex-title p {
          color: #5191FA;
        }

      }

      .button:hover .pop-menue {
        display: block !important;
        transform: scale(1);

      }


      .flex-title:hover {
        cursor: pointer;

      }


    }

  }

  /* mobile screen */
  .mobile-screen {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    display: none;

    .button-menue {
      i {
        font-size: $sizeIconsMain;
        color: $colorBlack;
      }

      &:hover {
        cursor: pointer;
      }
    }

  }

  /* stop mobile screen */
  /* responsive */
  @media(max-width:1000px) {
    .larg-screen {
      display: none;

    }

    .mobile-screen {
      display: flex;
    }
  }

  @media(min-width:901px) and (max-width: 1000px) {
    .larg-screen {
      zoom: 0.9;
    }
  }

  /* stop responsive */
  /* stop larg screen */

}}