@import '../options/variables.scss';
body {
  background-color: #F5F7FA;
}
.login-up {
  padding: 15px;
  padding-top: 0px;

  .flex-content {
    padding: 0px;
    margin-top: 100px;
    margin-bottom: 50px;
    display: flex;
    overflow: hidden;
    height: 560px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    border-radius: 25px;

    .left {
      width: 40%;
      position: relative;
      height: 100%;

      img {
        width: 100%;
        height: 100%;
      }

      .flex-btn {
        position: absolute;
        bottom: 70px;
        left: 50%;
        transform: translate(-50%);
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: white;
        box-shadow: rgba(255, 255, 255, 0.35) 0px 5px 15px;
        width: 270px;
        max-width: 100%;
        padding-top: 12px;
        padding-bottom: 12px;

        .title {
          color: $colorBlack;
          font-size: $sizeMainText;

        }

        .btn {
          color: $colorWhite;
          font-size: $sizeMainText;
          background-color: $colorPrimary;
          padding: 5px;
          padding-left: 15px;
          padding-right: 15px;
          border-radius: 10px;
          margin-left: 10px;
          text-decoration: none;
        }
      }

    }

    .right {
      padding-left: 30px;
      padding-right: 30px;
      width: 60%;
      background-color: $colorWhite;
      display: flex;
      flex-direction: column;
      justify-content: center;

      form {
        width: 100%;

        .title {
          color: $colorBlack;
          font-size: $sizeLargText;

        }

        .sub-tile {
          color: $colorSubBlack;
          font-size: $sizeMainText;
          margin-top: 20px;
        }

        .flex {
          display: flex;
          justify-content: space-between;
          margin-top: 25px;

          .leftt,
          .rightt {
            width: 48%;

            label {
              color: $colorBlack;
              font-size: $sizeMainText;

            }

            input[type="text"] {
              padding: 10px;
              font-size: $sizeMainText;
              color: $colorBlack;
              outline: none;
              border: 1px solid rgb(147, 146, 146);
              border-radius: 5px;
              margin-top: 5px;
              width: 100%;
            }
          }
        }

        .column {
          padding-top: 25px;

          p {
            color: $colorSubBlack;
            font-size: $sizeMainText;

          }

          button {
            color: $colorWhite;
            font-size: $sizeMainText;
            padding-top: 14px;
            padding-bottom: 14px;
            padding-left: 30px;
            padding-right: 30px;
            border: none;
            outline: none;
            border-radius: 10px;
            background-color: $colorPrimary;
            margin-top: 25px;

            &:hover {
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}

@media (min-width:3px) and (max-width:440px) {
  .login-up {
    .flex-content {
      zoom: 0.9;
      height: auto;

      .right {
        form {
          .flex {
            flex-direction: column;

            .leftt {
              width: 100%;
            }

            .rightt {
              width: 100%;
            }
          }
        }
      }

    }
  }

}

@media (max-width:835px) {
  .login-up {
    .flex-content {
      zoom: 0.9;
      flex-direction: column;
      height: auto;

      .left {
        width: 100%;
        height: 370px;
      }

      .right {
        width: 100%;
        padding-top: 70px;
        padding-bottom: 65px;
      }

    }
  }

}

@media (min-width:836px) and (max-width:1022px) {
  .login-up {
    .flex-content {
      zoom: 0.9;
    }
  }

}